import React from 'react';
import Img from 'gatsby-image';
import injectSheet from 'react-jss';

import Link from '../components/Link';

const Header = ({ classes, resolutionsLogo, height }) => {
    const pages = [
        { label: 'Home', to: '/' },
        { label: 'Use case', to: '/usecase' },
        { label: 'FAQ', to: '/faq' },
        { label: 'Forum', to: 'https://www.reddit.com/r/onairvr/' },
        { label: 'Downloads', to: 'downloads'}
    ];

    const links = pages.map((link, index) => {
        return (
            <li key={index} className={classes.linkItem}>
                <Link className={classes.link} key={index} to={link.to}>
                    {link.label}
                </Link>
            </li>
        );
    });
    
    return (
        <nav className={classes.container}>
            <Img
                outerWrapperClassName={classes.logo}
                resolutions={resolutionsLogo}
                />
            <ul className={classes.linkList}>
                {links}
            </ul>
        </nav>
    );
};

const padding = 15;

const styles = {
    container: {
        position: 'fixed',
        top: 0,
        zIndex: 10,
        width: '100%',
        height: (props) => props.height - padding * 2,
        margin: 0,
        padding: padding,
        backgroundColor: '#183C61',
        display: 'flex',
        alignItems: 'center'
    },
    logo: {
        display: 'inline-block'
    },
    linkList: {
        marginLeft: 'auto',
        paddingRight: padding,
        display: 'flex',
        color: 'white'
    },
    linkItem: {
        listStyleType: 'none'
    },
    link: {
        fontFamily: [ 'Noto Sans Regular', 'san-serif' ],
        fontSize: '0.7rem',
        color: 'white',
        margin: '0 1em',
        padding: '0 1em',
        textTransform: 'uppercase',
        letterSpacing: '0.32em',
        textDecoration: 'none'
    }
};

export default injectSheet(styles)(Header);



