import React from 'react';
import Helmet from 'react-helmet';
import injectSheet from 'react-jss';

import './index.css';
import Header from '../components/Header';

const headerHeight = 84;

const Layout = ({ classes, children, data }) => {    
    return (
        <div>
            <Helmet
                title={data.site.siteMetadata.title}
                meta={[
                    { name: 'description', content: 'Sample' },
                    { name: 'keywords', content: 'sample, something' },
                ]}
                />
            <Header
                resolutionsLogo={data.imageLogo.childImageSharp.resolutions}
                height={headerHeight}
                />
            <article className={classes.content}>
                {children()}
            </article>
        </div>
    );
};

const styles = {
    content: {
        marginTop: headerHeight
    }
};

export default injectSheet(styles)(Layout);

export const query = graphql`
    query IndexLayoutQuery {
        ...IndexLayoutFragment
    }

    fragment IndexLayoutFragment on RootQueryType {
        site {
            siteMetadata {
                title
            }
        }
        imageLogo: file(relativePath: { eq: "onairvr_logo.png" }) {
            childImageSharp {
                resolutions(width: 177) {
                    ...GatsbyImageSharpResolutions
                }
            }
        }        
    }
`;



